import QSWavesButton from '@/components/QS-WavesButton/QS-WavesButton.vue';
export default {
	components: {
		QSWavesButton
	},
	onLoad() {
		this.clickUrl = false; //点击超时
	},
	data() {
		return {
			AddClass: false,
			clickUrl: false,
			btnList: [{
					//preIconType: 'scan'  图标
					//wavesColor: 'rgba(255,255,255,.6)',  自定义颜色
					type: 'default',
					txt: '入库收货',
					disabled: false,
					plain: true, //镂空的波纹按钮
				},
				{
					type: 'default',
					txt: '搜索首页',
					disabled: false,
					plain: true,
				},
				{
					type: 'default',
					txt: '还大包',
					disabled: false,
					plain: true,
				},
				{
					type: 'default',
					txt: '移库',
					disabled: false,
					plain: true,
				},
				{
					type: 'default',
					txt: '发货出库',
					disabled: false,
					plain: true,
				},
				{
					type: 'default',
					txt: '退出',
					disabled: false,
					plain: true,
				},
			]
		}
	},
	methods: {
		clickFc(btnList, index) {
			//console.log(index);
			//console.log(btnList[index].txt);

			//预防重复点击 通过变量开关来控制  以防万一添加定时
			if (this.clickUrl == true) {
				console.log('已提交');
				return;
			}

			this.clickUrl = true;
			setTimeout(() => {
				this.clickUrl = false;
			}, 1500);

			var Url = '';
			var text = btnList[index].txt;
			this.AddClass = true;
			if (text == '入库收货') {
				Url = '/pages/backend/intowarehouse';
			} else if (text == '搜索首页') {
				Url = '/pages/backend/searchhome';
			} else if (text == '还大包') {
				Url = '/pages/backend/returnbigbags';
			} else if (text == '移库') {
				Url = '/pages/backend/movegoods';
			} else if (text == '发货出库') {
				Url = '/pages/backend/outofwarehouse';
			} else if (text == '退出') {
				Url = '/pages/zaizai-login/index';

				uni.setStorage({
					key: 'userName',
					data: '',
				});
				
				uni.setStorage({
					key: 'token',
					data: '',
				});

			}

			if (Url) {
				console.log(Url);
				uni.navigateTo({
					url: Url
				});

			}
			console.log(text);



			//uni.showToast({
			//	title: '点击了:' + text
			//})
		},
		getuserinfo(e) {
			console.log(JSON.stringify(e))
		}
	}
}
